import ListItemAvatar from "@mui/material/ListItemAvatar";
import ListItemText from "@mui/material/ListItemText";
import Avatar from "@mui/material/Avatar";
import Grid from "@mui/material/Grid";
import { CustomIconButton } from "./listItem.styled";
import DeleteIcon from "@mui/icons-material/Delete";
import * as React from "react";
import { styled } from "@mui/material/styles";
import { CustomContactsIcon } from "./listItem.styled";
import { CustomListItem } from "./listItem.styled";
import { CustomLink } from "./listItem.styled";
import "./listItem.css";
interface ListContactProps {
  API: string;
  handleDeleteClick: (contactId: string) => void;
  num: number;
}

export const AlignItemsList: React.FC<ListContactProps> = ({
  API,
  handleDeleteClick,
  num,
}) => {
  const Demo = styled("div")(({ theme }) => ({
    backgroundColor: theme.palette.background.paper,
  }));

  return (
    <>
      <CustomLink to={`${API}`} className="contact">
        <Grid item xs={12} md={6}>
          <Demo>
            <CustomListItem
              secondaryAction={
                <CustomIconButton
                  aria-label="delete"
                  size="small"
                  onClick={() => handleDeleteClick(API)}
                >
                  <DeleteIcon fontSize="inherit" />
                </CustomIconButton>
              }
            >
              <ListItemAvatar>
                <Avatar
                  alt={`Avatar nÂ°${num}`}
                  src={`https://picsum.photos/id/2${num}/200/300`}
                />
              </ListItemAvatar>
              <ListItemText primary={API} secondary="" />
              <CustomContactsIcon fontSize="small" />
            </CustomListItem>
          </Demo>
        </Grid>
      </CustomLink>
    </>
  );
};
