import React from "react";
import { useSelector } from "react-redux";
import "./search.css";
import Box from "@mui/material/Box";
import { styled } from "@mui/material/styles";
import TextField from "@mui/material/TextField";
import AccountCircle from "@mui/icons-material/AccountCircle";
import { InputProps } from "@mui/material";
interface Props {
  handleOnChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
}

export const SearchComponent: React.FC<Props> = ({ handleOnChange }) => {
  const filtered = useSelector((state: any) => state.contacts.filter);
  const CustomBox = styled(Box)`
    margin-bottom: 10px;
  `;
  return (
    <CustomBox sx={{ display: "flex", alignItems: "flex-end" }}>
      <AccountCircle sx={{ color: "action.active", mr: 1, my: 0.5 }} />
      <input type="text" value={filtered} onChange={handleOnChange} />
    </CustomBox>
  );
};
